.reviews {
    padding: 112px 360px;
}

.comments {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-wrap: wrap;
    &-author {
        width: 50%;
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        align-items: stretch;
        &--photo {
            border-radius: 50%;
        }
        &--text {
            text-align: left;
        }
    }
}